export default class MovieDB {
    _apiBaseForItem = 'https://api.themoviedb.org/3/movie/550?api_key=3c79bfcb56de9dd546762f6bf11c9ae7';

    getResource = async (url) => {
        const res = await fetch(`https://api.themoviedb.org/3/list/${url}?api_key=3c79bfcb56de9dd546762f6bf11c9ae7&language=ru-RU`);
        if(!res.ok) {
            throw new Error(`Could not fetch ${url}` +
                `, received ${res.status}`) 
        }
        return await res.json();
    }

    getList = async () => {
        const res = await this.getResource('3');
        return await res.items.map(movie => this._transformList(movie));
    }

    _transformList = (movie) => {
        return {
            adult: movie.adult,
            vote: movie.vote_average,
            title: movie.title,
            pathImg: `https://image.tmdb.org/t/p/w500${movie.backdrop_path}`,
            review: movie.overview,
            date: movie.release_date
        };
    };
}