import './App.css';
import React from 'react';
import Header from "./components/Header/Header";
import SearchFilm from "./components/SearchFilm/SearchFilm";
import FilmCards from "./components/FIlmCards/FilmCards";
import FilmDetails from "./components/FilmDetails/FilmDetails";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import Registration from './components/Registration/Registration';
import Join from './components/Join/Join';
import MovieDB from './MovieDB/MovieDB';

export default class App extends React.Component {
    state = {
        films: [],
        searchFilter: '',
        inputEvent: ''
    }

    updateStateFilms = () => {
        const movie = new MovieDB();
        movie.getList()
            .then(res => {
                console.log('work...');
                const newArr = [...this.state.films];
                res.forEach(item => {
                    const newObjWithFilm = {
                        name: item.title,
                        yearRelease: item.date,
                        img: item.pathImg,
                        adult: item.adult,
                        midRaiting: item.vote,
                        review: item.review,
                        trailer: '_-iXjRm3jG0',
                        reviews: [],
                        raiting: 0
                    }
                    newArr.push(newObjWithFilm);
                });
                this.setState({
                    films: newArr
                });
            })
    }

    addNewComment = (id) => {
        const oldArr = [...this.state.films];
        const textArea = document.querySelector('.AddNewCommentTextArea');
        if (this.state.inputEvent) {
            oldArr[id].reviews.push({
                name: 'Valdemar293',
                comment: this.state.inputEvent,
                CounterRaiting: 0
            });
        }
        this.setState({
            films: oldArr,
            inputEvent: ''
        })
        textArea.value = '';
    }

    ChangeInputData = (e) => {
        this.setState({
            inputEvent: e.target.value
        })
    }

    changeFilmRaiting = (id, raiting) => {
        const newArr = [...this.state.films];
        newArr[id].raiting = raiting;
        this.setState({
            films: newArr
        })
    }

    componentDidMount() {
        this.updateStateFilms();
    }

    searchHandler = (e) => {
        this.setState({
            searchFilter: e.target.value
        })
    }

    render() {
        return (
            <Router>
                <div className='App'>
                    <Header allFilms={this.state.films}/>
                    <SearchFilm searchHandler={this.searchHandler}/>
                    <Switch>
                        <Route exact path={'/'} render={(match) => <FilmCards searchFilter={this.state.searchFilter} renderItems={this.state.films}/>}/>
                        <Route path={'/join'} component={Join}/>
                        <Route path={'/reg'} component={Registration}/>
                        <Route path={`/:id`} render={({match}) => {
                            return <FilmDetails updateStateFilms={this.updateStateFilms} ChangeInputData={this.ChangeInputData} addNewComment={this.addNewComment} film={match.params.id} changeRaiting={this.changeFilmRaiting} films={this.state.films}/>
                        }}/>
                    </Switch>
                </div>
            </Router>
        )
    }
}